import axios from 'axios';
import React, { useEffect, useState } from 'react';
import { useHistory, useParams } from 'react-router-dom';
import styled from 'styled-components';
import './Detail.scss';

let Box = styled.div`
    padding-top: 30px;
`;
let Title = styled.h4`
    font-size: 25px;
    color: ${ props => props.color } 
`;


// class Detail2 extends React.Component {

//     componentDidMount() { //Detail2 컴포넌트가 Mount 되었을때 실행할 코드~

//     }
    
//     componentWillUnmount() { //Detail2 컴포넌트가 Unmount 되기 직전에 실행할 코드~

//     }
// }


function Detail(props) {

    let [alert, alertChange] = useState(true);
    let [inputData, inputDataChange] = useState('');

    useEffect(()=>{ //컴포넌트가 mount, update 될때, 특정 코드를 실행할 수 있음

        axios.get() //Detail 컴포넌트 등장 & 업데이트시 실행할 코드

        let timer = setTimeout(()=>{ alertChange(false) }, 2000); //2초 후에 alert 창을 안보이게 
        console.log('타이머 실행');
        return ()=>{ clearTimeout(timer) } //<Detail>이 사라질때 타이머 제거
    },[]); //[] 업데이트시엔 실행안되게,  [alert]alert라는 state가 변경이 될때만



    let { id } = useParams(); //:id자리에 사용자가 입력한 값

    // - Detail.js에 데이터바인딩하실 때 0번째 상품의 제목을 여기 보여주세요~ 라고 썼는데 이게 아니라
    // 상품의 영구번호가 0인 상품의 제목을 여기 보여주세요~ 하시면 됩니다.
    // 영구번호는 shoes라는 상품 데이터안에 함께 저장되어있습니다.
    // 현재 shoes라는 상품데이터들 안엔 {id : 0} 이런 영구번호가 있습니다.
    // 그럼 현재 /:id 자리에 입력한 값과 영구번호가 같은 {상품데이터}를 찾아서
    // 데이터바인딩해주면 되는게 아닐까요.
    // find() 라는 ES6 신문법이 있습니다. Array 안에서 원하는 자료를 찾고싶을 때 사용합니다.
    // filter() 함수, 그냥 반복문 이런거 쓰셔도 전혀상관없습니다.

    // 1. find()는 array 뒤에 붙일 수 있으며, 안에 콜백함수가 들어갑니다.
    // 2. 콜백함수 내의 파라미터는 (제가 상품이라고 적은거) array 안에 있던 하나하나의 데이터를 의미합니다.
    // 3. return 오른쪽엔 조건식을 적을 수 있습니다. 이게 참인 데이터만 새로운 변수에 저장해줍니다.
    // 4. 조건식엔 그리고 그걸 현재 URL의 /:id에 적힌 값과 상품의 영구번호 (상품.id)가 같은지 비교하고 있는 겁니다.

    // 그래서 /detail/0으로 접속시 찾은상품이라는 변수를 출력해보시면 아마 영구번호가 id : 0인 데이터가 나올겁니다.
    // /detail/1로 접속시 찾은상품이라는 변수는 영구번호가 id : 1인 데이터일겁니다.
    // 그래서 찾은상품이라는 변수를 이용해서 상품명, 가격 HTML 부분에 데이터바인딩을 했을 뿐입니다.
    // 성공!

    // 지금은 프론트엔드에서 모든 데이터를 다루고 있어서 어려운 + 반복문스러운 find() 함수를 사용한 것이지만
    // 실제 개발할 땐 그냥 서버에 id : 0인 상품데이터를 Ajax로 요청하는 경우가 많을겁니다.
    // 그럼 저렇게 find() 어쩌구를 쓰는게 아니라 ajax 요청하는 코드가 들어가있겠고
    // ajax 요청을 성공하면 {} 중괄호 안에 깔끔하게 상품데이터가 하나만 딱 들어올 것 같군요.
    let findProduct = props.shoes.find(function(product){
        return product.id == id
    });

    let history = useHistory(); //방문기록 등을 저장해놓는 object

    return (
      <div className="container">
        <Box>
            <Title className="red">상세페이지</Title>
        </Box>

        { inputData }
        <input onChange={(e)=>{ inputDataChange(e.target.value) }}/>

        {
            alert === true
            ? (<div className="my-alert2">
                    <p>재고가 얼마 남지 않았습니다</p>
                </div>
              )
            : null
        }
        

        <div className="row">
          <div className="col-md-6">
            <img src="https://codingapple1.github.io/shop/shoes1.jpg" width="100%" />
          </div>
          <div className="col-md-6 mt-4">
            <h4 className="pt-5">{findProduct.title}</h4>
            <p>{findProduct.content}</p>
            <p>{findProduct.price}</p>

            <Info stock={props.stock}></Info>

            <button className="btn btn-danger" onClick={()=>{ props.stockChange([9,11,12]) }}>주문하기</button> 
            <button className="btn btn-danger" onClick={()=>{
                history.push('/')
              }}>뒤로가기</button> 
          </div>
        </div>
      </div>
    )
}

function Info(props){
    return (
        <p>재고: {props.stock[0]}</p>
    );
}

export default Detail;